gin-bottom: 0px;\n}\n\n.full {\n  width: 100%;\n}\n\n.half {\n  width: calc(50% - 50px) !important;\n}\n\n.leadHelper {\n  margin-bottom: 30px;\n}\n\n.fit {\n  max-width: fit-content;\n}\n\n.link {\n  width: 100%;\n}\n\n.table {\n  &__wrapper {\n    width: 100%;\n    margin-top: 100px;\n    min-height: 70vh;\n  }\n\n  &__header {\n    width: 100%;\n    padding: 20px;\n    display: flex;\n    gap: 20px;\n    align-items: center;\n    background: var(--background);\n    border-radius: 6px;\n  }\n\n  &__columnHeader {    \n    &_sorting {\n      user-select: none;\n      cursor: pointer;\n    }\n\n    &_label {\n      color: var(--gray);\n    }\n\n    &_main {      \n      flex: 1;\n    }\n\n    &_secondary {\n      flex: 2;\n    }\n  }\n\n  &__content {\n    margin: 15px 0;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    flex-wrap: wrap;\n    gap: 10px;\n  }\n\n  &__row {\n    padding: 25px 20px;\n    width: 100%;\n    display: flex;\n    gap: 20px;\n    align-items: center;\n    border-radius: 6px;\n    border: 1px solid transparent;\n    cursor: pointer;\n    transition: .2s;\n\n    &:hover {\n      border: 1px solid var(--blue);\n      box-shadow: 0px 5px 60px rgba(140, 145, 181, 0.15);\n      transition: .2s;\n    }\n\n    &_main {\n      flex: 1;\n    }\n\n    &_secondary {\n      flex: 2;\n    }\n  }\n\n  &__footer {\n    width: 100%;\n    padding: 30px 40px;\n    display: flex;\n    gap: 20px;\n    align-items: center;\n    justify-content: space-between;\n    background: var(--background);\n    border-radius: 6px;\n\n    &Info *{\n      color: var(--gray);\n    }\n\n    &Pagination {\n      display: flex;\n      align-items: center;\n      gap: 20px;\n\n      &Controller {\n        display: flex;\n        align-items: center;\n        gap: 4px;\n      }\n\n      & * {\n        color: var(--gray);\n      }\n    }\n\n    &Control {\n      width: 8px;\n      height: 16px;\n      cursor: pointer;\n    }\n  }\n\n  &__paginator {\n    border: none;\n  